/* ========================================================================
* DOM-based Routing
* Based on http://goo.gl/EUTi53 by Paul Irish
*
* Only fires on body classes that match. If a body class contains a dash,
* replace the dash with an underscore when adding it to the object below.
*
* .noConflict()
* The routing is enclosed within an anonymous function so that you can
* always reference jQuery with $, even when in .noConflict() mode.
* ======================================================================== */

(function($) {
    if (!window.location.origin) {
        window.location.origin = window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' +
        window.location.port: '');
    }

    if(document.location.hostname === 'localhost' || document.location.hostname === 'offsiderz.test') {
        $.ajaxurl = window.location.origin + '/wp/wp-admin/admin-ajax.php';
    } else {
        $.ajaxurl = window.location.origin + '/wp-admin/admin-ajax.php';
    }
    
    /**
     * Function use to get window sizes
     * @return {Object} sizes
     */

    $.getViewportSize = (function() {
        function getViewportSize() {
            var html = document.documentElement;

            return {
                width : window.innerWidth || html.clientWidth || document.body.clientWidth,
                height : window.innerHeight || html.clientHeight || document.body.clientHeight
            };
        }

        return getViewportSize;
    }());

    var $body = $('body');

    /**
     * Throttle
     * 
     * @param {Function} callback
     * @param {Number} delay
     */

    $.throttle = (function(callback, delay) {
        function throttle(callback, delay) {
            var last;
            var timer;

            return function() {
                var now = +new Date();
                var context = this;
                var args = arguments;

                if(last && now < last + delay) {
                    clearTimeout(timer);

                    timer = setTimeout(function() {
                        callback.apply(context, args);
                        last = now;
                    }, delay);
                } else {
                    callback.apply(context, args);
                    last = now;
                }
            };
        }
        return throttle;
    }());

    /**
     * isEmail
     *
     * Test email address against regular expression to confirm validity
     *
     * @param {email} email
     */
    var isEmail = function (email) {
        email = email.trim();
        var regExp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return regExp.test(email);
    };

    /**
     * Set quantity selector
     */

    function setQuantitySelectors() {
        var $quantitySelector = $('.quantity-selector');
        var $minus = $quantitySelector.find('.minus');
        var $plus = $quantitySelector.find('.plus');

        if($quantitySelector.length) {
            $minus.on('click', function(event) {
                var $input = $(this).parent('.quantity-selector').find('input.qty');
                var val = parseInt($input.val());
                var step = $input.attr('step');
                step = 'undefined' !== typeof(step) ? parseInt(step) : 1;

                if (val > 0) {
                    $input.val( val - step ).change();
                }
            });

            $plus.on('click', function(event) {
                var $input = $(this).parent('.quantity-selector').find('input.qty');
                var val = parseInt($input.val());
                var step = $input.attr('step');
                step = 'undefined' !== typeof(step) ? parseInt(step) : 1;
                $input.val( val + step ).change();
            });
        }
    }

    /**
     * L10N
     */
    var l10n = {
        errors : {
            required : 'Champs obligatoire',
            email: {
                empty: 'Champs obligatoire',
                invalid: 'Email non valide'
            }
        }
    };

    // Use this variable to set up the common and page specific functions. If you
    // rename this variable, you will also need to rename the namespace below.
    var Offsiderz = {
        // All pages
        'common': {
            init: function() {
                // JavaScript to be fired on all pages

                /**
                 * Single Product
                 */

                var $quantitySelector = $('.quantity-selector');

                if($quantitySelector.length) {
                    setQuantitySelectors();
                }

                /**
                 * Mini Cart
                 */

                var $removeBtn = $('.mini_cart_item a.remove');
                if($removeBtn.length) {
                    $removeBtn.on('click', function(event) {
                        event.preventDefault();

                        var product_id = $(this).attr("data-product_id");
                        var cart_item_key = $(this).attr("data-cart_item_key");
                        var product_container = $(this).parents('.mini_cart_item');

                        product_container.block({
                            message: null,
                            overlayCSS: {
                                cursor: 'none'
                            }
                        });

                        $.ajax({
                            type: 'POST',
                            dataType: 'json',
                            url: $.ajaxurl,
                            data: {
                                action: "ofs_ajax_product_remove",
                                product_id: product_id,
                                cart_item_key: cart_item_key
                            },
                            success: function(response) {
                                // console.log(response);

                                if(!response || response.error){ return; }

                                var fragments = response.fragments;

                                // Replace fragments
                                if(fragments) {
                                    $.each(fragments, function(key, value) {
                                        $(key).replaceWith(value);
                                    });
                                }
                            }
                        });
                    });
                }

                /**
                 * Lazy Loading
                 * 
                 * @required : https://github.com/dinbror/blazy
                 */

                var $lazy = $('.lazy');
                if($lazy.length) {
                    $.bLazy = new Blazy({
                        selector: '.lazy',
                        successClass: 'loaded',
                        success: function(element) {
                            if($(element).parent('.loader').length) {
                                $(element).parent('.loader').removeClass('loader');
                            }
                        },
                        error : function(element, error) {
                            console.log(error);
                        }
                    });
                }

                /**
                 * Contact Form 
                 */

                var $contactForm = $('form#contact-form');
                if($contactForm.length) {
                    var inputs = $contactForm.find('input');
                    var $requiredFields = $(inputs).filter('[required]:visible');

                    $contactForm.find('button[type=submit]').on('click', function (event) {
                        event.preventDefault();

                        var $nameField  = $('#contact-name');
                        var $emailField = $('#contact-email');
                        var $msgField   = $('#contact-message');

                        var name  = $nameField.val();
                        var email = $emailField.val();
                        var msg   = $msgField.val();

                        // Name Field
                        if(name === '') {
                            $nameField.parent('.form-group').addClass('error');
                            $nameField.parent('.form-group').find('small').html(l10n.errors.required);
                        } else {
                            $nameField.parent('.form-group').removeClass('error');
                            $nameField.parent('.form-group').find('small').html('');
                        }

                        // Email Field
                        if(email === '') {
                            $emailField.parent('.form-group').addClass('error');
                            $emailField.parent('.form-group').find('small').html(l10n.errors.email.empty);
                        } else if(!isEmail(email)) {
                            $emailField.parent('.form-group').addClass('error');
                            $emailField.parent('.form-group').find('small').html(l10n.errors.email.invalid);
                        } else {
                            $emailField.parent('.form-group').removeClass('error');
                            $emailField.parent('.form-group').find('small').html('');
                        }

                        // Msg Field
                        if(msg === '') {
                            $msgField.parent('.form-group').addClass('error');
                            $msgField.parent('.form-group').find('small').html(l10n.errors.required);
                        } else {
                            $msgField.parent('.form-group').removeClass('error');
                            $msgField.parent('.form-group').find('small').html('');
                        }

                        // Submit
                        if((name !== '') && isEmail(email) && (msg !== '')) {
                            $contactForm.find('button[type=submit]').addClass('loading');

                            $.ajax({
                                type: 'POST',
                                url: $.ajaxurl,
                                data: {
                                    'action' : 'ofs_contact',
                                    'name' : name,
                                    'from' : email,
                                    // 'tel' : $('#contact-form-phone').val()
                                    'msg' : msg
                                },
                                success: function(response) {
                                    $contactForm.find('button[type=submit]').removeClass('loading');

                                    response = JSON.parse(response);

                                    if(response.success) {
                                        $contactForm.find('.alert').removeClass('is-visible');
                                        $contactForm.find('.alert.alert-success').addClass('is-visible');
                                    }
                                },
                                error: function(response) {
                                    $contactForm.find('button[type=submit]').removeClass('loading');
                                    
                                    $contactForm.find('.alert').removeClass('is-visible');
                                    $contactForm.find('.alert.alert-error').addClass('is-visible');
                                }
                            });
                        }
                    });
                }

                /**
                 * Scroll event
                 */

                $(window).on('scroll', $.throttle(function(event) {
                    var currentSrollPos = $(this).scrollTop();

                    // Add class to update header style
                    if($(this).scrollTop() >= 100) {
                        $body.addClass('scrolled');
                    } else {
                        $body.removeClass('scrolled');
                    }
                }, 200));
            },
            finalize: function() {
                $(document.body).on('updated_cart_totals', function(event) {
                    var $quantitySelector = $('.quantity-selector');

                    if($quantitySelector.length) {
                        setQuantitySelectors();
                    }
                });

                if($('.woocommerce-ordering').length) {
                    $('select.orderby').select2();
                }

                if($('#pa_taille').length) {
                    $('#pa_taille').select2({
                        minimumResultsForSearch: -1,
                        placeholder: '---',
                    });
                }
            }
        }
    };

    // The routing fires all common scripts, followed by the page specific scripts.
    // Add additional events for more control over timing e.g. a finalize event
    var UTIL = {
        fire: function(func, funcname, args) {
            var fire;
            var namespace = Offsiderz;
            funcname = (funcname === undefined) ? 'init' : funcname;
            fire = func !== '';
            fire = fire && namespace[func];
            fire = fire && typeof namespace[func][funcname] === 'function';

            if (fire) {
                namespace[func][funcname](args);
            }
        },
        loadEvents: function() {
            // Fire common init JS
            UTIL.fire('common');

            // Fire page-specific init JS, and then finalize JS
            $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
                UTIL.fire(classnm);
                UTIL.fire(classnm, 'finalize');
            });

            // Fire common finalize JS
            UTIL.fire('common', 'finalize');
        }
    };

    // Load Events
    $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
