/**
 * @name Sliders
 * @description Transform Sliders to Carousels
 * @author Sebastien Nguyen
 * @version 1.0.0
 * @required https://github.com/OwlCarousel2/OwlCarousel2
 */

(function($, window, document) {
    "use strict";

    var activeSliders = [];

    /*********************
    * Destroy
    *********************/

    var destroyCarousel = function($element, index) {
        $element.removeClass('owl-carousel');
        $element.trigger('destroy.owl.carousel');

        activeSliders.splice(activeSliders.indexOf(index), 1);
    };

    /*********************
    * Create
    *********************/

    var createCarousel = function($element, index, options) {
        $element.addClass('owl-carousel');
        $element.addClass('owl-theme');

        var responsive;
        var slideCount = $element.find('.slider-item').length;

        if(options.type === 'responsive') {
            responsive = {
                576: {
                    margin: 10,
                    nav: true,
                    dots: false,
                    items : 2
                },
                768: {
                    margin: 15,
                    nav: true,
                    dots: false,
                    items : 3
                },
                930: {
                    margin: 15,
                    nav: true,
                    dots: false,
                    items : 4
                }
            };
        } else {
            responsive = {
                991: {
                    margin: 15,
                    nav: true,
                    dots: true,
                    items : 3
                }
            };
        }

        var defaults = {
            margin: 10,
            loop: false,
            items: 1,
            navClass: [
                'icon icon-round icon-chevron-left',
                'icon icon-round icon-chevron-right'
            ],
            responsive: responsive,
            autoHeight: false,
            onTranslated: function() {
                var $lazy = $('.lazy');
                if($lazy.length) {
                    $.bLazy.revalidate();
                }
            }
        };

        var settings = $.extend({}, defaults, options);

        $element.owlCarousel(settings);

        activeSliders.push(index);

        // $element.equalize({children: '.card'});
    };

    /*********************
     * Dom Ready
     *********************/

    $(document).ready(function(){
        var $sliders = $('[data-slider]');

        if($sliders.length) {

            /**
             * Transform Sliders to Carousels if touchevent isn't supported
             * if is mobile or tablet & slides length > 1
             * if is desktop & slide length > 3
             */
            if(!Modernizr.addTest('touchevents').touchevents) {
                $sliders.each(function(index) {
                    var $this = $(this);
                    var slidesCount = $this.find('.slider-item').length;
                    var options = $this.data('slider');

                    if(($.getViewportSize().width <= 991 && slidesCount > 1) || ($.getViewportSize().width > 991 && slidesCount > 3)) {
                        createCarousel($(this), index, options);
                    }
                });

                /*********************
                 * Resize event
                 *********************/

                $(window).on('resize', $.throttle(function(event) {
                    $sliders.each(function(index) {
                        var $this = $(this);
                        var slidesCount = $this.find('.slider-item').length;
                        var options = $this.data('slider');

                        // if current slider isn't active
                        if($.inArray(index, activeSliders) < 0) {
                            if(($.getViewportSize().width <= 991 && slidesCount > 1) || ($.getViewportSize().width > 991 && slidesCount > 3)) {
                                createCarousel($this, index, options);
                            }
                        } else {
                            if(($.getViewportSize().width >= 991 && slidesCount <= 3)) {
                                destroyCarousel($this, index);
                            }
                        }
                    });
                }, 500));
            }
        }
    });
}(window.jQuery, window, document));
