(function($, window, document) {
    "use strict";

	var $trigerSearch = $('.trigger-search-overlay');
	var	$overlay = $('.search-ovelay');

    var transEndEventNames = {
		'WebkitTransition' : 'webkitTransitionEnd',
		'MozTransition'    : 'transitionend',
		'OTransition'      : 'oTransitionEnd',
		'msTransition'     : 'MSTransitionEnd',
		'transition'       : 'transitionend'
    };
    // var transEndEventName = transEndEventNames[ Modernizr.prefixed('transition') ];

    function toggleOverlay() {
        if($overlay.hasClass('open')) {
            $overlay.removeClass('open');
            $overlay.addClass('closing');

            var onEndTransitionFn = function(event) {
                $trigerSearch.removeClass('open');
                $overlay.removeClass('closing');
            };

            if( Modernizr.csstransitions ) {
				$overlay.on( transEndEventName, onEndTransitionFn );
            } else {
                onEndTransitionFn();
            }
        } else if(!$overlay.hasClass('closing')) {
            $trigerSearch.addClass('open');
            $overlay.addClass('open');
            $overlay.find('.form-control').focus();
        }
    }

	/**
	 * Dom Ready
	 */
    $(document).ready(function(){
    	$trigerSearch.on( 'click', function(event) {
            event.preventDefault();
            toggleOverlay();
        });
    });

}(window.jQuery, window, document));
